.formContainer {
    width: 100%;
}

#postingUploadContainer {
    max-width: 500px;
    width: 50%;
    min-width: 300px;
    margin: 30px auto;
}

div.dropzone div {
    width: 100% !important;
    height: 100px !important;
}

div.dropzone {
    margin: 15px auto !important;
}

input.abstract {
    margin: 10px auto;
}

input.meetingLocation {
    margin: 10px auto;
}

p#dropDescription {
    margin: 1rem;
}

div#priceContainer {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: end;
        align-items: flex-end;
}

span#dollar {
    margin: auto 5px;
    font-size: 25px;
}

span#dot {
    margin: 0 5px;
    font-size: 25px;
}

input#priceDollars {
    text-align: right;
}

div.postingStatus {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
}

span.spanStatus {
    margin-right: 15px;
}

span.mandatoryStar {
    color: red;
}

select#categorySelector {
    width: 100%;
    padding: 10px;
}

p#locationInfo {
    margin-bottom: 0px;
    font-size: 12px;
}

span.removeImageBtn {
    margin-left: 10px;
    border-radius: 5px;
    background-color: #5050ff;
    color: white;
    padding: 10px;
}


