#no-padding {
	padding: 0
}

#tabs {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  overflow: hidden;
  height: 3em
}

#tabs button {
	background-color: #2bbbad;
  outline: none;
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1em;
  margin: 0em 0.25em 0.3em 0.25em;
  padding: 0.5em 1.5em 0.5em 1.5em;
  border-radius: 0.25em;
  cursor: pointer
}

#tabs.expand {
  overflow: visible;
  height: auto
}

#expand {
  margin-right: 1em;
  float: right;
  position: absolute;
  right: -0.3em;
  font-size: 1.2em;
  padding: 0.2em;
  border: solid #e3f2fd;
  border-bottom-left-radius: 0.3em;
  border-bottom-right-radius: 0.3em;
  background: #e3f2fd;
  color: #007bff;
  cursor: pointer
}



